import ufoBlastorama from './UFOBlastorama.png';
import dropSpider from './DropSpiderLogo.png';
import './styles.css';

export default function VideoGames() {
  return (
    <div className="mainContent">
      <div>

        <div className="programmingLibraries">
          <div>
            <figure>
					  <a href="https://www.oculus.com/experiences/quest/4215799038511978/" target="_blank"><img className="gameImage" src={ufoBlastorama} alt="UFO Blastorama" /></a>
					  <figcaption>
							This is my first VR game and release for the Oculus Quest 2. It is
							written using the Unity Game Engine. It's a VR translation of the
							classic Space Invaders game. It's still in very early stages, and I
							plan on adding more to it in the future.
						</figcaption>
					</figure><br />
          </div>
        </div>

        <div className="programmingLibraries">
          <div>
            <figure>
              <a href="https://oceancucumber.itch.io/drop-spider" target="_blank"><img className="gameImage" src={dropSpider} alt="Drop Spider" /></a>
            </figure>
            <figcaption>
              This is my first 2D mobile game. This was written using the Godot Engine 3.3
              and submitted as an entry into the <a href="https://itch.io/jam/game-off-2021" target="_blank">Game Off 2021 Game Jam</a>.
            </figcaption><br /><br />
          </div>
        </div>



      </div>
    </div>
  );
};
