import './styles.css';
import githubLogo from './github.png';
import jQueryLogo from './jquery.png';
import javaScriptLogo from './JavaScript.png';

export default function Libraries() {
  return (
    <div className="mainContent">
      <div>
        <div className="programmingLibraries">
          <div>
            <h3><img src="https://cdn.cdnlogo.com/logos/r/18/rubygems.svg" width="30" height="30" />
            <a href="https://rubygems.org/gems/easystats" target="_blank">Easystats</a></h3>
            I made this Ruby Gem back in 2012 to add statistical functionaly to an array of
            numbers. Like if you had an array of numbers you can get the standard deviation
            of them pretty easily.<br />
            <a href="https://github.com/mgrigajtis/easystats" target="_blank"><img src={githubLogo} alt="GitHub Repo" /></a>
          </div>
        </div>

        <div className="programmingLibraries">
          <div>
            <h3><img src="https://cdn.cdnlogo.com/logos/r/18/rubygems.svg" width="30" height="30" />
            <a href="https://rubygems.org/gems/easyzpl" target="_blank">Easyzpl</a></h3>
            In 2014 I found the need to create labels on Zebra printers using their ZPL language,
						which is just absolutely miserable. I made this wrapper library in Ruby because the labels
						were being generated from a Ruby on Rails application.  It made the process less unbearable.<br />
						<a href="https://github.com/mgrigajtis/easyzpl" target="_blank"><img src={githubLogo} alt="GitHub Repo" /></a>
          </div>
        </div>

        <div className="programmingLibraries">
          <div>
            <h3><img src={jQueryLogo} width="30" height="30" />
            <a href="https://www.npmjs.com/package/jquery-fireworks" target="_blank">jquery-fireworks</a></h3>
            This plugin creates an HTML5 canvas in a division and animates exploding fireworks in random colors.<br />
            <a href="https://github.com/mgrigajtis/jquery-fireworks" target="_blank"><img src={githubLogo} alt="GitHub Repo" /></a>
          </div>
        </div>

        <div className="programmingLibraries">
          <div>
            <h3><img src={javaScriptLogo} width="30" height="30" />
            <a href="https://www.npmjs.com/package/holidaylights" target="_blank">Holiday Lights (and more)</a></h3>
            This is one of my personal favorites. It creates an animation of falling leaves in Autumn, falling snow in
            Winter, and Christmas Lights in December in addition to the snow.<br />
            <a href="https://github.com/mgrigajtis/HolidayLights" target="_blank"><img src={githubLogo} alt="GitHub Repo" /></a>
          </div>
        </div>

      </div>
    </div>
  );
};
