import React, { Component } from "react";
import './styles.css';
import avatar from './MetaQuestAvatar.png';
import * as waxjs from "@waxio/waxjs/dist";

export default class Home extends Component {
  constructor(props) {
    super(props);

    this.state = {
      balance: 0,
      wax: new waxjs.WaxJS({
        rpcEndpoint: 'https://wax.greymass.com',
        tryAutoLogin: false
      }),
      tipAmount: 0,
      tipRecipient: 'gjkzi.wam',
      errorMessage: ''
    }
  }

  async updateWaxBalance() {
    let balance = await this.state.wax.rpc.get_currency_balance('eosio.token', this.state.wax.userAccount);
    this.setState(state => {
      return {balance: balance}
    });
  }

  updateErrorMessage(message) {
    this.setState(state => {
      return {errorMessage: message}
    });
  }

  updateTipAmount(evt) {
    const val = evt.target.value;
    this.setState(state => {
      return {tipAmount: val}
    });
  }

  async SendWax() {
    if(!this.state.wax.api) {
      this.updateErrorMessage('You have been logged out, please log in again.');
      return;
    }

    try {
      const result = await this.state.wax.api.transact({
      actions: [{
        account: 'eosio.token',
        name: 'transfer',
        authorization: [{
          actor: this.state.wax.userAccount,
          permission: 'active',
        }],
        data: {
          from: this.state.wax.userAccount,
          to: this.state.tipRecipient,
          quantity: this.state.tipAmount + ' WAX',
          memo: 'Tip from home page.'
        },
    }]}, {
      blocksBehind: 3,
      expireSeconds: 1200
    });
      this.updateWaxBalance();
      this.updateErrorMessage(JSON.stringify(result, null, 2));
    } catch(e) {
      console.log(e);
      this.updateErrorMessage(e.message);
    }
  }

  // checks if autologin is available
  async autoLogin() {
      if (await this.state.wax.isAutoLoginAvailable()) {
          this.updateErrorMessage('AutoLogin enabled for account: ' + this.state.wax.userAccount + '<br/>Active: ' + this.state.wax.pubKeys[0] + '<br/>Owner: ' + this.state.wax.pubKeys[1]);

          (async () => {
             await this.updateWaxBalance();
          })(this);
      }
      else {
        this.updateErrorMessage('Not auto-logged in to your Wax Cloud account.  Click the Log In Button to log in.');
      }
  }

  // Normal login. Triggers a popup for non-whitelisted dapps
  async login() {
    try {
      // if autologged in, this simply returns the userAccount w/no popup
      await this.state.wax.login();

      (async () => {
         await this.updateWaxBalance();
      })(this);
    } catch (e) {
      this.updateErrorMessage(e.message);
    }
  }

  render() {
    let strLoginButtonClass = '';
    let strAccountInfoBalance = '';

    if (this.state.wax.userAccount) {
      strLoginButtonClass = 'hidden';
    } else {
      strAccountInfoBalance = 'hidden';
    }

    return (
      <div className="mainContent">
        <div>
          <div className="divAvatar">
            <img alt="My Oculus Avatar" src={avatar} />
          </div>
          <div className="divText">
            <h3>Hello!</h3>
            <p>I'm Matt the programmer. I'm a full-stack DotNet developer by day,
            but in the evening I like to take on some more interesting projects.
            Some of these projects include working with AI, writing block chain
            applications, creating 3D graphics and developing video games. This
            is a show case of some of my projects over the years, and also some
            tools I'm in the process of writing for <a href="https://r.upland.me/6o9n" rel="noreferrer" target="_blank">Upland</a>.
            </p>
          </div>
          <div className="divText">
            <div>
              <div>{this.state.errorMessage}</div>
              <div className={strLoginButtonClass}>
                  <button type="button" onClick={() => this.login(this)}>Wax Cloud Wallet Login</button>
              </div>
              <div className={strAccountInfoBalance}>
                Account: {this.state.wax.userAccount}<br />
                Balance: {this.state.balance}<br />
                <h3>Send me a tip!</h3>
                <input type="number" value={this.state.tipAmount} onChange={evt => this.updateTipAmount(evt)} />
                <button type="button" onClick={() => this.SendWax(this)}>Send</button>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  }
}
